export const getReservationsByDevice = `query GetReservationsByDevice($username: String!, $deviceId: String!, $currentDate: AWSDate!) {
  getReservationsByDevice(username: $username, deviceId: $deviceId, currentDate: $currentDate) {
    reservationCode
    listingId
    hostId
    username
    email
    phoneNo
    bookingId
    listingName
    guestName
    identityId
    memberCount
    checkInDate
    checkOutDate
    keyInfo
    active
    previousCountry
    previousCity
    nextCountry
    nextCity
    lastUpdateOn
  }
}
`

export const compareFaces = `query CompareFaces($imgBucket: String!, $idImgKey: String!, $faceImgKey: String!, $similarityThreshold: Float!) {
  compareFaces(imgBucket: $imgBucket, idImgKey: $idImgKey, faceImgKey: $faceImgKey, similarityThreshold: $similarityThreshold) {
    idMatches
    similarity
    messageCode
  }
}
`

export const updateReservation = `mutation UpdateReservation($hostId: String, $reservationCode: String!, $listingId: String!, $username: String, $email: String, $phoneNo: String, $bookingId: String, $listingName: String, $guestName: String, $identityId: String, $memberCount: Int, $checkInDate: AWSDate, $checkOutDate: AWSDate, $keyInfo: String, $active: Boolean, $previousCountry: String, $previousCity: String, $nextCountry: String, $nextCity: String, $lastUpdateOn: AWSDateTime, $expectedLastUpdateOn: AWSDateTime!) {
  updateReservation(input: { hostId: $hostId, reservationCode: $reservationCode, listingId: $listingId, username: $username, email: $email, phoneNo: $phoneNo, bookingId: $bookingId, listingName: $listingName, guestName: $guestName, identityId: $identityId, memberCount: $memberCount, checkInDate: $checkInDate, checkOutDate: $checkOutDate, keyInfo: $keyInfo, active: $active, previousCountry: $previousCountry, previousCity: $previousCity, nextCountry: $nextCountry, nextCity: $nextCity, lastUpdateOn: $lastUpdateOn }, expectedLastUpdateOn: $expectedLastUpdateOn) {
    reservationCode
    listingId
    hostId
    username
    email
    phoneNo
    bookingId
    listingName
    guestName
    identityId
    memberCount
    checkInDate
    checkOutDate
    keyInfo
    active
    previousCountry
    previousCity
    nextCountry
    nextCity
    lastUpdateOn
  }
}
`

export const getMembers = `query GetMembers($reservationCode: String!) {
  getMembers(reservationCode: $reservationCode) {
    reservationCode
    memberNo
    fullName
    imgBucket
    faceImgName
    faceImgKey
    idImgName
    idImgKey
    similarity
    idMatches
    idVerified
    lastUpdateOn
  }
}
`
export const createMember = `mutation CreateMember($reservationCode: String!, $memberNo: Int!, $fullName: String, $imgBucket: String, $faceImgName: String, $faceImgKey: String, $idImgName: String, $idImgKey: String, $similarity: Float, $idMatches: Boolean, $idVerified: Boolean, $lastUpdateOn: AWSDateTime) {
  createMember(input: { reservationCode: $reservationCode, memberNo: $memberNo, fullName: $fullName, imgBucket: $imgBucket, faceImgName: $faceImgName, faceImgKey: $faceImgKey, idImgName: $idImgName, idImgKey: $idImgKey, similarity: $similarity, idMatches: $idMatches, idVerified: $idVerified, lastUpdateOn: $lastUpdateOn }) {
    reservationCode
    memberNo
    fullName
    imgBucket
    faceImgName
    faceImgKey
    idImgName
    idImgKey
    similarity
    idMatches
    idVerified
    lastUpdateOn
  }
}
`

export const updateMember = `mutation UpdateMember($reservationCode: String!, $memberNo: Int!, $fullName: String,  $imgBucket: String, $faceImgName: String, $faceImgKey: String, $idImgName: String, $idImgKey: String, $similarity: Float, $idMatches: Boolean, $idVerified: Boolean, $lastUpdateOn: AWSDateTime, $expectedLastUpdateOn: AWSDateTime!) {
  updateMember(input: { reservationCode: $reservationCode, memberNo: $memberNo, fullName: $fullName, imgBucket: $imgBucket, faceImgName: $faceImgName, faceImgKey: $faceImgKey, idImgName: $idImgName, idImgKey: $idImgKey, similarity: $similarity, idMatches: $idMatches, idVerified: $idVerified, lastUpdateOn: $lastUpdateOn }, expectedLastUpdateOn: $expectedLastUpdateOn)
  {
    reservationCode
    memberNo
    fullName
    imgBucket
    faceImgName
    faceImgKey
    idImgName
    idImgKey
    similarity
    idMatches
    idVerified
    lastUpdateOn
  }
}
`