<template>
  <v-app>
    <top-menu/>
    <v-content>
      <router-view/>
    </v-content>
    <warning-dialog @closeWarningRefresh="onCloseWarningRefresh" @closeWarningSignOut="onCloseWarningSignOut"></warning-dialog>
  </v-app>
</template>

<script>
import Vue from 'vue'
// import Menu from '@/components/Menu.vue'
const Menu = () => import('@/components/Menu.vue')
// import WarningDialog from '@/components/WarningDialog.vue'
const WarningDialog = () => import('@/components/WarningDialog.vue')

export default {
  name: 'app',
  created() {
  },
  components: {
    warningDialog: WarningDialog,
    topMenu: Menu
  },
  methods: {
    async onCloseWarningSignOut() {
      this.$store.dispatch('session/logout')
        .catch(e => console.log('logout!!!', e))
    },
    async onCloseWarningRefresh() {
      this.$router.go()
    }
  }
}
</script>


