import appsync from '@/api/appsync'
import storage from '@/api/storage'
import aws_exports from '@/aws-exports'

const state = {
  reservation: null,
  reservations: [],
  members: [],
  currentStep: 1,
  currentMember: null,
}

const mutations = {
  setCurrentMember(state, val) {
    state.currentMember = val
  },  
  setReservations(state, val) {
    state.reservations = val
  },  
  setReservation(state, val) {
    state.reservation = val
  },
  setMembers(state, val) {
    state.members = val
  },
  setCurrentStep(state, val) {
    state.currentStep = val
  },
}

const actions = {
  async loadReservations ({ commit, dispatch, state, rootState }, val) {

    const rtn = await appsync.getReservationsByDevice(rootState.session.username, rootState.session.deviceInfo.deviceId, ((new Date).toISOString()).substring(0,10))

    if (rtn.errors) {
      throw rtn.errors[0]
    } else {
      if (rtn.data.getReservationsByDevice.length > 0) {

        const reservations = rtn.data.getReservationsByDevice.map(item => {
          item.selectText = item.listingName + ' ' + item.checkInDate
          return item
        })
        console.log('loadReservations', reservations)
        commit('setReservations', reservations)

        if (reservations.length === 1) {
          dispatch('pickReservation', reservations[0])
        }

      } else {
        throw new Error(Exceptions.CustomNoReservation.message)
      }
    }
  },
  pickReservation ({ commit }, val) {

    commit('setReservation', val)
    
  },
  clearReservation ({ commit }) {

    commit('setReservation', null)
    
  },
  async saveIdentityId ({ commit, state, rootState }) {

    state.reservation.identityId = rootState.session.identityId

    const rtn = await appsync.updateReservation(state.reservation)
    if (rtn.errors) {
      throw rtn.errors[0]
    } else {
      const reservation = rtn.data.updateReservation
      commit('setReservation', reservation)
    }

  },
  async loadMembers ({ commit, getters, state }) {

    const rtn = await appsync.getMembers(state.reservation.reservationCode)
    console.log('loadMembers rtn', rtn)
    if (rtn.errors) {
      throw rtn.errors[0]
    } else {
      const members = rtn.data.getMembers

      if (members.length <= state.reservation.memberCount) {
        const existingMemberNos = members.map((item) => {
          return item.memberNo
        })

        let i = 1
        // console.log('i:', i)
        for (i; i <= state.reservation.memberCount; i++) {
          if (existingMemberNos.indexOf(i) === -1) {
            members.push({ 
              reservationCode: state.reservation.reservationCode, 
              memberNo: i, 
              fullName: null, 
              similarity: 0,
              idVerified: false,
              idMatches: false,
              idImgSrc: require('@/assets/img/id.svg'),
              faceImgSrc: require('@/assets/img/face.svg'),
              idImgName: null,
              faceImgName: null,
              idImgKey: null,
              faceImgKey: null,
            })
          }
        }
      }

      members.sort((a, b) => {
        if (a.memberNo < b.memberNo)
          return -1
        if (a.memberNo > b.memberNo)
          return 1
        return 0
      })

      const resultMembers = await Promise.all(members.map(async (item) => {
        // console.log('Promise', item)
        if (!item.idImgName) {
          item.idImgSrc = require('@/assets/img/id.svg')
        } else {
          item.idImgSrc = await storage.get(item.idImgName)
        }

        if (!item.faceImgName) {
          item.faceImgSrc = require('@/assets/img/face.svg')
        } else {
          item.faceImgSrc = await storage.get(item.faceImgName)
        }
        return item

      }));

      commit('setMembers', resultMembers)
    }  

  },
  async saveMember ({ state, dispatch, getters }, isId) {
    if (state.currentMember.lastUpdateOn) {
      console.log('updateMember', state.currentMember)
      const rtn = await appsync.updateMember(state.currentMember)

      if (rtn.errors) {
        throw rtn.errors[0]
      } else {
        // console.log('members', members)
        const member = rtn.data.updateMember

        const index = state.members[member.memberNo - 1]

        state.members[index] = member
      }
    } else {
      console.log('createMember', state.currentMember)
      const rtn = await appsync.createMember(state.currentMember)
      
      if (rtn.errors) {
        throw rtn.errors[0]
      } else {
        // console.log('members', members)
        const member = rtn.data.createMember

        const index = state.members[member.memberNo - 1]

        state.members[index] = member
      }
    }

    if (!state.reservation.identityId) {
      await dispatch('saveIdentityId').catch(e => {
        console.error('saveIdentityId', e)
        throw e
      })
    } else {
      if (isId) {
        const allMembersInput = !state.members.some(crtMember => {
          if (!crtMember.idImgKey) {
            return true
          }
        })

        console.log('allMembersInput', allMembersInput)

        if (allMembersInput) {
          await dispatch('saveIdentityId').catch(e => {
            console.error('saveIdentityId', e)
            throw e
          })
        }
      }
    }


  },
  async putImage ({ commit, getters, state, rootState}, { img, isId }) {
    // state.currentMember.imgBucket = aws_exports.aws_user_files_s3_bucket
    state.currentMember.idMatches = false
    
    if (isId) {
      state.currentMember.idVerified = false
      state.currentMember.idImgName = `${state.reservation.reservationCode}/id/${state.currentStep}.jpg`
      state.currentMember.idImgKey = `protected/${rootState.session.identityId}/${state.reservation.reservationCode}/id/${state.currentStep}.jpg`

      await storage.put(state.currentMember.idImgName, img.split(',')[1])

      state.currentMember.idImgSrc = await storage.get(state.currentMember.idImgName)
    } else {
      state.currentMember.faceImgName = `${state.reservation.reservationCode}/face/${state.currentStep}.jpg`
      state.currentMember.faceImgKey = `protected/${rootState.session.identityId}/${state.reservation.reservationCode}/face/${state.currentStep}.jpg`

      await storage.put(state.currentMember.faceImgName, img.split(',')[1])

      state.currentMember.faceImgSrc = await storage.get(state.currentMember.faceImgName)
    }
  },
  setCurrentStep ({ commit, state }, val) {
    commit('setCurrentStep', val)
    commit('setCurrentMember', state.members[val - 1])
    console.log('state.currentMember', state.currentMember)

  },
  async rekogId ({ commit, getters, state}) {
    const res = await appsync.compareFaces({
      imgBucket: aws_exports.aws_user_files_s3_bucket,
      idImgKey: state.currentMember.idImgKey,
      faceImgKey: state.currentMember.faceImgKey,
      similarityThreshold: 85
    })

    state.currentMember.idMatches = res.idMatches
    state.currentMember.similarity = res.similarity
    state.currentMember.messageCode = res.messageCode    
  },
  async saveLocations ({ commit, state, rootState }, val) {
console.log('saveLocations', val)
    state.reservation.previousCountry = val.previousCountry
    state.reservation.previousCity = val.previousCity
    state.reservation.nextCountry = val.nextCountry
    state.reservation.nextCity = val.nextCity

    const rtn = await appsync.updateReservation(state.reservation)
    if (rtn.errors) {
      throw rtn.errors[0]
    } else {
      const reservation = rtn.data.updateReservation
      commit('setReservation', reservation)
    }

  },
}

const getters = {
    steps: state => {
      return state.members.length
    },
    currentStep: state => {
      return state.currentStep
    },
}

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations
}